
  /* Estilos para la sección de búsqueda */
  .search-section {
    width: 30%;
    background-color: #eeeeeea7;
    padding: 10px;
    box-sizing: border-box;
     
  }
  
  .search-section h2 {
    color: #092C50;
    margin-bottom: 10px;
  }
  
  .search-box {
    margin-top: 5%;
    margin-bottom: 5px;
    flex-grow: 1;
  }
  
  .search-box input[type="text"],
  .search-box input[type="number"],
  .search-box select,
  .search-box select:focus {
    background-color: #ffffff;
    padding: 5px;
    font-family: Arial, sans-serif;
    margin-right: 5px;
    border: 1px solid #007bff;
    margin-bottom: 4px;
    width: 50%;
  }
  
  .search-box button {
    padding: 5px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
   
  }
  
  .search-box button:hover {
    background-color: #0056b3;
  }
  
  /* Estilos para la tabla de resultados de búsqueda */
  .search-section table {
    border-collapse: collapse;
    transform: scale(0.97);/*esto es para reducir el porcentaje del tamaño de la tabla*/
  }
  
  .search-section table, 
  .search-section table th, 
  .search-section table td {
    border: 1px solid #092C50;
    background-color: #ffffff;
  }
  
  .search-section table th,
  .search-section table td {
    text-align: left;
    padding: 5px;
    color: #092C50;
    font-size:11px;
  }
  
  .search-section table th {
    background-color: #092C50;
    color: white;
  }
  .search-section button {
    padding: 5px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .search-section button:hover {
    background-color: #0056b3;
  }
  .search-section button.imagenb {
   
    background-color: #ffffff;
    border: none;
    cursor: pointer;
  }
  .search-section button:hover.imagenb  {
    background-color: #bed8f381;
  }
  .search-section button.imagen-seleccionada {
   
    .imagen-boton { 
    background-color: #108b22ac;
    border: none;
    cursor: pointer;
    text-align: center;
    }
  }
  .imagen-boton {
    width: 15px;
    height: 15px;
  }

  /* Estilos para la sección de detalles */
  .details-section {
    width: 70%;
    background-color: #ffffff;
    padding: 5px;
    margin-right: auto;
    box-sizing: border-box;
    flex-grow: 2;
    
  }
  
  .details-section label {
    margin-right: 10px;
    font-weight: bold;
    color: #092C50;
  }
  
  .details-section select,
  .details-section button {
    margin-right: 10px;
  }
  
  /* Estilos para la tabla de detalles de sala */
  .details-section table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 3px;
    color: #092C50;
    font-size: small;
  }
  
  .details-section table,
  .details-section table th,
  .details-section table td {
    border: 0.5px solid #092C50;
  }
 
  
  .details-section table th,
  .details-section table td {
    text-align: center;
    padding: 3px;
    
  }
  
  .details-section table th {
    background-color: #092C50;
    color: white;
  }
  
  /* Botones de cambio de semana */
  .details-section button {
    padding: 5px 12px;
    background-color: #007bff;
    color: white;
    border: none;
    cursor: pointer;
  }
  
  .details-section button:hover {
    background-color: #0056b3;
  }

  /* Estilos para la tabla */
.table-modulos {
  width: 100%;
  border-collapse: collapse;
  text-align: left;
}

.table-modulos th, .table-modulos td {
  border: 1px solid #ddd;
  padding: 8px;
}

/* Estilos para las cabeceras de la tabla */
.table-modulos thead th {
  background-color: #092C50;
  color: white;
}

/* Estilo específico para las subcolumnas de 'Módulo' */
.table-modulos th.subcolumna, .table-modulos td.subcolumna {
  border-right: none; /* Elimina el borde derecho para simular una única columna */
}

.table-modulos td.subcolumna:last-child {
  border-right: 1px solid #ddd; /* agrega el borde derecho a la última subcolumna */
}

/* Estilos para la fila del encabezado de 'Módulo' */
.table-modulos th[colspan="2"] {
  text-align: center;
}

/* Opcionales: Estilo para mejorar la visualización de las filas */
.table-modulos tr:nth-child(even) {
  background-color: #f2f2f2;
}

.table-modulos tr:hover {
  background-color: #ddd;
}
.div-centrado {
  width: 50%; /* para ajustar el ancho */
  margin:  auto; /* Centra el div horizontalmente */
  padding: 20px;
  
}
.titulo-disp-salas {
  width: 100%;
  text-align: left;
  margin-right: 10px;
  font-weight: bold;
  background-color: #092C50; /* color de fondo*/
